.card-continer{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:column;
        flex-direction:column;
    background-color: #fff;
    border: 10px solid transparent;
    -webkit-border-image: url(./border-img.png) 30;
         -o-border-image: url(./border-img.png) 30;
            border-image: url(./border-img.png) 30;
    border-radius: 5px;
    padding: 1rem;
    cursor:url(https://emoji.gg/assets/emoji/2355_ultra_ball_pokemon.png), auto;
    -moz-osx-font-smoothing: grayscale;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-transition: -webkit-transform 0.25s ease-out;
    transition: -webkit-transform 0.25s ease-out;
    -o-transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
    box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.8);
    -webkit-box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.8);
    -moz-box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.8); 
    font-family: 'Righteous', cursive;
}

.card-continer:hover{
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

img{
    width: 160px;
    height: 180px;
    display:block;
    margin-left: auto;
    margin-right: auto;
    cursor:url(https://emoji.gg/assets/emoji/2355_ultra_ball_pokemon.png), auto;
}
a, a:link, a:visited, a:focus, a:active {
    color: #000000;
    text-decoration: none;
    cursor:url(https://emoji.gg/assets/emoji/2355_ultra_ball_pokemon.png), auto;
}

